.App {
  display: flex;
  background-color: #f3f3f3;
  padding: 50px;
}

.control {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding-left: 100px;

  & > * {
    width: 80px;
    height: 80px;
    padding: 10px;
    border-radius: 50%;
    background: rgb(134, 211, 247);
    border: 2px solid rgb(35, 113, 150);
  } 
}

.h1 {
  font-size: 48px;
}

.time {
  font-size: 24px;
}

.show {
  display: block;
}

.hide {
  display: none;
}
